<template>
  <div class="lyrics">
    <div class="lyricsContainer" ref="a">
      <div class="center" v-show="touchmove" >
        <van-icon name="play" class="name" @click="changeTime"/>
        <div class="centerLine"></div>
        <div class="time">{{touchMoveTime}}</div>
      </div>
      <div class="blank"></div>
      <!-- <p v-for="(value, index) in [1, 2, 3]" :key="index">{{value}}</p> -->
      <p v-for="(value, index) in lyricsWords" :key="index" :class="{active: index === nIndex}" class="p">{{value}}</p>
      <div class="blank"></div>
    </div>
  </div>
</template>

<script>
import { onMounted, onUpdated, reactive, ref, watch } from 'vue'
import {secToMinSec} from '../utils/timeFormat.js'
export default {
  name: 'Lyrics',
  props: ['lyricsTimes', 'currentTime', 'lyricsWords'],
  setup(props, context){
    let a = ref();
    let lyricsTimes = props.lyricsTimes;   //
    let lyricsWords = props.lyricsWords;   //
    let lyricsTop = reactive([]);   //1
    let lyricsTopComputedTimes = ref(0);  //
    let nIndex = ref();   //
    let touchmove = ref(false);   //
    let touchMoveTime = ref()   //
    let touchMoveLyricsIndex = ref()   //
    let timer = reactive([])    //touchend
    
    //
    let getLyricsScrollTop = () => {
      lyricsTopComputedTimes.value ++;  //onUpdated
      let arr = document.querySelectorAll('.p');
      let preScrollTop = 0    //0
      lyricsTop.push(preScrollTop);    //0
      arr.forEach((i, index) => {
          let preP;   //
          let nowP = arr[index]     //
          let prePHeight = 0;   //0
          let nowPHeight =  parseInt(getComputedStyle(nowP,null).height);  //
          let margin = parseInt(getComputedStyle(nowP,null).margin);   //
          if(index !== 0){    //
            preP = arr[index - 1];    //
            prePHeight =  parseInt(getComputedStyle(preP,null).height);  //
            margin = margin * 2;    //
          }
          let res = prePHeight / 2 + nowPHeight / 2 + margin    //
          let newScrollTop = preScrollTop + res;    //DOM
          lyricsTop.push(newScrollTop);
          preScrollTop = newScrollTop;
      })
    }
    watch([() => props.currentTime, touchmove], () => {
      if(touchmove.value) return;   //
      //
      //  
      lyricsTimes.forEach((i, index) => {
        if(i <= props.currentTime && (lyricsTimes[index + 1]>props.currentTime || index + 1 === lyricsTimes.length /*  */)){
          // if(nIndex.value === index) return;    //nIndexindex
          a.value.scrollTop = lyricsTop[index + 1];   //lyricsTop11
          nIndex.value = index    //
        }
      })
    })
    onMounted(() => {
      //
      a.value.addEventListener('touchmove', () => {   //
                                                            //touchmovetouchendtouchmove
                                                            //touchmovetouchmove
                                                            //touchmovetouchend
        //
        if(timer.length !== 0){
          timer.forEach(i => {
            clearTimeout(i)
          })
          timer.length = 0;
        }
        touchmove.value = true
        //scrollTop
        let arr = document.querySelectorAll('.p');
        let lineY = document.querySelector('.centerLine').getBoundingClientRect().y;
        let pY = [];
        arr.forEach(p => { pY.push(p.getBoundingClientRect().y) })
        pY.forEach((p, index) => {
          if(pY[index] <= lineY && pY[index+1] > lineY){
            touchMoveTime.value = secToMinSec(lyricsTimes[index]);
            touchMoveLyricsIndex.value = index + 1;
          }
        })
      });
      //
      a.value.addEventListener('touchend', (e) => {
        timer.push(setTimeout(() => {
          touchmove.value = false
          timer.length = 0;
        }, 2000))
      });
    })
    onUpdated(() => {
      if(lyricsTopComputedTimes.value === 0)    //
        getLyricsScrollTop();
    })
    let changeTime = () => {
      context.emit('changeTime', touchMoveTime.value)
      touchmove.value = false;
    }
    return {
      lyricsWords,
      nIndex,
      touchmove,
      touchMoveTime,
      changeTime,
      a
    }
  },
}
/* 

  
    1
      1audiotimeupdate
      2v-foronUpdatedscrollTop(touchmovetop)
      3watchJSDOMscrollTop
    2
      1touchmovewatch
      2touchmove
      3y
        yyyy
      4touchendidtouchmove
    3
      watch
    41s
      touchmovefalse
  bugs    
    
      DOMDOMDOM
    touchmovetop
      scrollToptop
    touchmovetouchend
      
    nIndexindexDOM
      
    
      touchend
    
      CSDN
    
      external

    
*/ 
</script>

<style scoped>
  .lyrics{
    width: 340px;
    height: 130px;
    background-color: white;
    opacity: 0.5;
    margin-top: 20px;
    border-radius: 70px;
  }
  .lyricsContainer{
    width: 100%;
    height: 100%;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    scroll-behavior: smooth;
  }
  .lyricsContainer::-webkit-scrollbar {display:none}  /**/
  .blank{
    width: 100%;
    height: 65px;
    flex-shrink: 0;
  }
  .active{
    color: red;
  }
  p{
    margin: 6px;
    font-size: 15px;
    word-break: break-all;
    max-width: 250px;
    text-align: center;
  }
  .center{
    position: fixed;
    top: 365px;
    transform: translateY(-50%);
    width: 100%;
    display: flex;
    align-items: center;
  }
  .name{
    font-size: 18px;
  }
  .centerLine{
    width: 80%;
    margin: 0 10px;
    height: 1px;
    background-color: gray;
    opacity: 0.5;
    z-index: -1;
  }
  .time{
    font-size: 12px;
  }
</style>